.add__block {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;

	margin-bottom: 30px;
}

.add__input {
	-ms-flex-positive: 1;
	    flex-grow: 1; 
	
	margin-right: 5px;
	padding: 10px;
	padding-left: 20px;

	color: #000000;
	font-size: 20px;
	font-family: 'Roboto Condensed', 'Arial', sans-serif;
	font-weight: 300;

	border: 1px solid #cccccc;
	border-radius: 3px;
}

.add__input::-webkit-input-placeholder {
	color: #cccccc;
	font-size: 20px;
	font-family: 'Roboto Condensed', 'Arial', sans-serif;
	font-weight: 300;
}

.add__input:-ms-input-placeholder {
	color: #cccccc;
	font-size: 20px;
	font-family: 'Roboto Condensed', 'Arial', sans-serif;
	font-weight: 300;
}

.add__input::-ms-input-placeholder {
	color: #cccccc;
	font-size: 20px;
	font-family: 'Roboto Condensed', 'Arial', sans-serif;
	font-weight: 300;
}

.add__input::placeholder {
	color: #cccccc;
	font-size: 20px;
	font-family: 'Roboto Condensed', 'Arial', sans-serif;
	font-weight: 300;
} 

.add__input:focus::-webkit-input-placeholder {
	opacity: 0; 
	-webkit-transition: opacity 0.4s ease; 
	-o-transition: opacity 0.4s ease; 
	transition: opacity 0.4s ease;
} 

.add__input:focus:-ms-input-placeholder {
	opacity: 0; 
	-webkit-transition: opacity 0.4s ease; 
	-o-transition: opacity 0.4s ease; 
	transition: opacity 0.4s ease;
} 

.add__input:focus::-ms-input-placeholder {
	opacity: 0; 
	-webkit-transition: opacity 0.4s ease; 
	-o-transition: opacity 0.4s ease; 
	transition: opacity 0.4s ease;
} 

.add__input:focus::placeholder {
	opacity: 0; 
	-webkit-transition: opacity 0.4s ease; 
	-o-transition: opacity 0.4s ease; 
	transition: opacity 0.4s ease;
}

.add__btn {
	-ms-flex-positive: 0;
	    flex-grow: 0;

	width: 40px;
	height: 40px;
	margin: 0;
	padding: 0;
	
	background-color: transparent;
	border: none;
	cursor: pointer;
}

.btn-check:hover,
.add__btn:hover {
	-webkit-transform: scale(1.1);
	    -ms-transform: scale(1.1);
	        transform: scale(1.1);
}




